/* Home */
.bg-home {
  background-image: url(../Img/break-section2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  position: relative;
}
.bg-home::after {
  content: '';
  position: absolute;
  background-color: rgba(0, 0, 0, 0.7);
  bottom: 0;
  display: block;
  width: 100%;
  height: 100%;
}
/* Break Section */
.break-section {
  background-image: url(../Img/break-section3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
